<template>
  <div class="head">
    <div class="header-in">
      <div class="logo" @click="$router.push('/')"><img src="../assets/logo.png" alt=""></div>
      <div :class="active===1?'header-item active':'header-item'" @click="switchActive('/')" style="width: 50px;">首页</div>
      <div :class="active===2?'header-item active':'header-item'" @click="switchActive('/home')" style="width: 100px;margin: 0 20px;">沪深港通</div>
      <div :class="active===3?'header-item active':'header-item'" @click="switchActive('/holdQuery')" style="width: 110px;">最近持股查询</div>
      <div class="login">
        <div class="input-wrap">
          <input type="text" placeholder="搜索">
        </div>
        <div class="login-wrap" @click="loginDialog=true">登录</div>
      </div>
    </div>
    <a-modal
      class="login-modal"
      style="top: 200px;width: 520px;"
      :title="null"
      :footer="null"
      :visible="loginDialog"
      :bodyStyle="bodyStyle"
      :maskClosable=true
      :closable=false
      @cancel="closeDialog"
    >
      <div class="close-icon"><img src="../assets/close.png" alt="" @click="closeDialog"></div>
      <div class="login-dialog-title">第三方登录平台</div>
      <div class="login-dialog-text">注册成为我们的用户，可以享受我们提供的更多品质服务，整个登录过程都会在第三方平台完成，请放心使用，我们不会记录您的任何私有信息。</div>
      <div class="wx-login" @click="handleLogin">
        <div class="wx-icon"><img src="../assets/wx.png" alt=""></div>
        <div class="wx-text">微信登录</div>
      </div>
    </a-modal>
  </div>
</template>

<script>
export default {
  name: 'Header',
  data() {
    return {
      active: 1,
      loginDialog: false,
      bodyStyle: {
        width: '520px',
        height: '520px',
        padding: '20px',
        borderRadius: '20px',
        margin: '0 auto'
      }
    }
  },
  mounted() {
    // 刷新页面保证 active 随路由切换
    switch (this.$route.matched[0].path) {
      case '':
        this.active = 1
        break
      case '/home':
        this.active = 2
        break
      case '/holdQuery':
        this.active = 3
        break
      case '/holderQuery':
        this.active = 3
        break
      default:
        this.active = 0
        break
    }
  },
  methods: {
    handleLogin() {
      window.open('https://open.weixin.qq.com/connect/qrconnect?appid=wxff89c882d87a2b2e&response_type=code&scope=snsapi_login&redirect_uri=https%3A%2F%2Fwww.lixinger.com%2Fauth-callback%3Flogin_method%3Dwechat%26next%3D')
    },
    switchActive(name) {
      this.$router.push(name)
    },
    closeDialog() {
      this.loginDialog = false
    }
  }
}
</script>
